.header {
    min-height: 60px;
    display: flex;
    background-color: rgb(233, 233, 233);
    position: sticky;
    top: 0;
    z-index: 100;
}
.header__menu {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 0px 20%;
}

.header .header__nav a{
    color: black;
    text-decoration: none;
    padding: 0.5rem;
    font-weight: bold;
}

.header .header__nav a:hover {
    transition: 150ms;
    border: 2px solid black;

}
.header__menu__logo img {
    width: 50px;
    object-fit: contain;
    margin: 0 -50px;
}